    //             `Cat${num % 2}`, 100)));
    //     return {
    //         products,
    //         selectedCategory: "All",
    //         order: new Order()
    //     }
    // },
    computed: {
        ...mapState<StoreState>({
            selectedCategory: (state: StoreState) => state.selectedCategory,
